.mob-slider{
  margin-top: 2.5em;
  .swiper{
    @media screen and (min-width: 993px){
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
        gap: 1.875em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
  &._col4{
    @media screen and (min-width: 993px){
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(4,calc(25% - .9375em));
          gap: 1.25em;
        }
      }
    }
  }
  &._col5{
    @media screen and (min-width: 993px){
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(5,calc(20% - 6em / 5));
          gap: 1.5em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .mob-slider{
    margin-top: 2.1875em;
    .swiper{
      &-container{
        width: calc(100% + 1.75em);
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 50%;
        padding-right: 1.75em;
      }
    }
    &._col5{
      .swiper{
        &-container{
          width: calc(100% + 2em);
        }
        &-slide{
          width: calc(100% / 3);
          padding-right: 2em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .mob-slider{
    margin-top: 2em;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 98%;
        padding-right: .75em;
      }
    }
    &._col5{
      .swiper{
        &-container{
          width: calc(100% + .625em);
        }
        &-slide{
          width: 50%;
          padding-right: .625em;
        }
      }
    }
  }
}
