.page-top{
    padding: 1.25em 0;
    &__inner,
    &__desc{
        @include grid100gap(1.25em);
    }
    &__desc{
        p{
            line-height: inherit;
        }
    }
}

.breadcrumbs {
    &__item {
        font-size: 1em;
        line-height: 140%;
        font-family: $font2;
        position: relative;
        font-weight: 400;
        display: inline;
        color: #838383;
        &:not(:first-child) {
            margin-left: 3em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                width: .875em;
                height: .75em;
                top: .3125em;
                right: 100%;
                margin-right: 1.3125em;
            }
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        &__item{
            font-size: .75em;
            &:not(:first-child){
                margin-left: 2em;
                &:before{
                    width: 1em;
                    height: 1em;
                    top: .2em;
                    margin-right: .66em;
                }
            }
        }
    }
}
