.footer{
  padding: 2.6875em 0 4.75em;
  background-color: $darkBlue;
  color: #fff;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col{
    padding-right: 1em;
    min-width: 12.25em;
    max-width: 16em;
    &:last-child{
      padding-right: 0;
    }
  }
  &__dropdown{
    margin-top: 1.25em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    color: #D9D9D9;
    &:not(:last-child){
      margin-bottom: .625em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__bottom{
    margin-top: 6em;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__pay{
    width: 13.75em;
    margin-right: 3.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    margin-right: auto;
  }
  &__by,
  &__link{
    font-size: 1em;
    line-height: 140%;
  }
  &__by{
    a{
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.9375em 0 2.3125em;
    &__inner{
      flex-wrap: wrap;
    }
    &__col{
      max-width: 33%;
      &:nth-child(4){
        width: 100%;
        margin-top: 4em;
      }
    }
    &__wrap{
      position: relative;
    }
    &__bottom{
      position: absolute;
      bottom: 0;
      right: 0;
      width: 60%;
      flex-wrap: wrap;
      text-align: right;
    }
  }
  @media screen and (min-width: 577px){
    .footer{
      &__by{
        order: 4;
      }
      &__pay{
        width: 100%;
        display: flex;
        justify-content: flex-end;
        order: -1;
        margin: 0;
        img{
          width: 13.75em;
        }
      }
      &__link2{
        margin-left: 1.6875em;
      }
      &__c{
        margin: 1.25em 0;
      }
    }
  }
}





@media screen and (max-width: 576px){
  .footer{
    padding: 1.5em 0 2.25em;
    &__inner{
      display: block;
    }
    &__col{
      max-width: unset;
      min-width: unset;
      padding: 0 0 .625em;
      &:nth-child(4){
        margin-top: 2em;
        padding: 0;
      }
    }
    &__toggle{
      display: flex;
      align-items: center;
      &:after{
        content: '';
        display: block;
        flex-shrink: 0;
        width: 1.5em;
        height: 1.5em;
        background-image: url(../img/arrow-down.svg);
        @include bgImgSize(100%);
        margin-left: .3125em;
        transition: all .3s;
      }
      &._active{
        &:after{
          transform: rotate(-180deg) translateY(-.0625em);
        }
      }
    }
    &__dropdown{
      display: none;
      padding-bottom: 1.25em;
    }
    .header-contacts__items{
      max-width: 13em;
    }
    &__bottom{
      margin-top: 2.5em;
      position: static;
      text-align: left;
      justify-content: unset;
      width: 100%;
    }
    &__c,
    &__link2,
    &__by{
      font-size: .75em;
    }
    &__pay{
      margin: .8125em 0;
    }
    &__link2{
      margin-right: 3.5em;
    }
  }
}
