.promo-page{
  padding: 1.875em 0 3.125em;
  .tab-content{
    margin-top: 2.5em;
  }
  .promo-card{
    &__img{
      padding-bottom: 92.1%;
    }
  }
  .product-detail__top{
    margin-top: 2.1875em;
  }
}

.promo-item{
  padding-bottom: 1.875em;
  &__img{
    width: 56.25em;
    max-width: 100%;
    display: block;
    border-radius: .5em;
    margin: 0 auto 2.375em;
  }
}


@media screen and (max-width: 992px){
  .promo-page{
    padding: 1.25em 0 3.625em;
    .product-detail__top{
      margin-top: 1.25em;
    }
  }

  .promo-item{
    padding-bottom: 1.25em;
    &__img{
      margin-bottom: 3em;
    }
  }
}

@media screen and (max-width: 576px){
  .promo-page{
    padding-top: 0;
    .content-nav{
      margin-top: 2.8125em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .promo-item{
    padding-bottom: 1.5625em;
    &__img{
      margin-bottom: 1.25em;
    }
  }
}
