.orders{
  &__badge{
    padding: 1.25em 2.8125em;
    font-family: $font2;
    background-color: $lightGrey;
    border-radius: .5em;
    border: $border2;
    display: flex;
    span{
      font-size: 1.125em;
      font-weight: 500;
      line-height: 140%;
    }
  }
  &__items{
    margin-top: 3.125em;
    @include grid100gap(3.125em);
  }
}

.order-card{
  border: $border2;
  font-family: $font2;
  &__top{
    padding: .625em 2.8125em;
    display: flex;
    align-items: center;
    min-height: 5.375em;
  }
  &__bottom{
    padding: 1.875em 2.8125em;
    border-top: $border2;
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-right: .375em;
    height: 3.25em;
    padding: 0 2.1875em;
    font-weight: 600;
    font-family: $font1;
  }
  &__cancel{
    margin: 0 0 0 auto;
  }
}

.order-info,
.parameter-card{
  .spoiler-toggle{
    margin-top: .3125em;
  }
  .filter-select{
    margin-top: .3125em;
    .nice-select{
      padding: 0;
      height: unset;
      border: none;
    }
    .disabled{
      display: none;
    }
  }
}

.order-info{
  border-top: $border2;
  padding: 1.875em 2.8125em;
  &__wrap{
    display: flex;
    margin-top: .5em;
  }
  &__content{
    padding-top: .4375em;
  }
  &__badge{
    @include center;
    padding: 0 1em;
    font-weight: 500;
    margin-left: 1.875em;
    border: 1px solid $grey2;
    border-radius: .5em;
    height: 2.5em;
    color: $grey2;
    span{
      font-size: 1em;
    }
  }
  &__sum{
    margin-top: .9375em;
    span{
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__status{
    display: flex;
    align-items: center;
    margin-top: 1em;
    &-title{
      font-size: 1em;
      line-height: 140%;
    }
    &-badge{
      padding: 0 1em;
      font-weight: 500;
      color: $grey2;
      border: 1px solid $grey2;
      border-radius: .5em;
      height: 1.875em;
      @include center;
      margin-left: .625em;
      background: $grey3;
      span{
        font-size: .875em;
      }
    }
  }
}

.order-item{
  &__back{
    margin-top: 1.875em;
    display: flex;
    padding-bottom: 1em;
    border-bottom: $border2;
    font-weight: 500;
    color: $grey2;
    font-family: $font2;
    span{
      font-size: 1.125em;
      line-height: 140%;
    }
    img{
      width: .75em;
      flex-shrink: 0;
      display: block;
      margin-right: .625em;
    }
  }
  &__subtitle{
    padding-left: 1.25em;
    margin-top: 1.875em;
    font-weight: 500;
    font-family: $font2;
    span{
      font-size: 1.125em;
      line-height: 140%;
    }
  }
}

.order-detail{
  padding: 1.875em 1.25em;
  border-radius: .5em;
  border: $border2;
  margin-top: 1.875em;
  max-width: 54.375em;
  &__wrap{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 10.875em auto auto 10.875em;
    gap: 2.5em;
    align-items: flex-start;
  }
  &__prop{
    @include grid100gap(.3125em);
    &-title{
      font-size: .875em;
      line-height: 140%;
      color: $grey2;
    }
    &-desc{
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__nav{
    @include grid100gap(.9375em);
    grid-row: 1/3;
    grid-column: 4/5;
  }
  &__btn{
    padding: 0;
    width: 100%;
  }
  &__more{
    align-self: flex-end;
    margin-top: -.5em;
  }
}

.order-contain,
.parameters{
  margin-top: 3.125em;
  .order-item__subtitle{
    margin: 0;
    font-family: $font1;
    font-weight: 600;
  }
}

.parameter-card{
  border: $border2;
  padding: 1.875em 1.25em;
  border-radius: .5em;
  margin-top: 1.875em;
  &__top{
    @include grid100gap(.625em);
    border-bottom: $border2;
    margin-bottom: 1.875em;
    padding-bottom: 1.875em;
  }
  &__content{
    display: grid;
    grid-template-columns: 9.375em calc(100% - 9.375em - 1.875em);
    gap: 1.875em;
  }
  &__img{
    height: 5em;
    padding: 1.25em;
    @include center;
    border: $border2;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 1.875em - 8.5em) 8.5em;
    gap: 1.875em;
    max-width: 36.5em;
    ._full{
      grid-column: 1/-1;
    }
  }
  &__text{
    @include grid100gap(.9375em);
  }
  &__sum{
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: .3125em;
      }
    }
  }
  &__badges{
    padding-top: 1.75em;
    .order-info__badge{
      margin: 0;
      width: 100%;
      padding: 0;
    }
  }
}

.order-table{
  margin-top: 1.875em;
  border: $border2;
  border-radius: .5em;
  font-family: $font2;
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,
  td{
    font-weight: 400;
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    text-align: right;
    border-bottom: $border2;
    width: 8.25em;
    &:first-child{
      padding-left: 1.25em;
      text-align: left;
      width: 27em;
    }
    &:last-child{
      padding-right: 1.25em;
    }
  }
  th{
    span{
      font-size: .875em;
      line-height: 140%;
      color: $grey2;
    }
  }
  &__bottom{
    padding: 1.25em;
    @include grid100gap(.3125em);
  }
  &__item{
    display: grid;
    grid-template-columns: 13.125em 13.125em;
    gap: 1.875em;
    justify-content: flex-end;
    text-align: right;
    &-title,
    &-desc{
      font-size: 1em;
      line-height: 140%;
    }
  }
}

.order-row{
  &__info{
    display: grid;
    grid-template-columns: 3em calc(100% - 4.875em);
    gap: 1.875em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 150%;
  }
  &__prop{
    &-title{
      display: none;
    }
    &-desc{
      font-size: 1em;
      line-height: 150%;
    }
  }
  ._price,
  ._sale,
  ._sum{
    font-weight: 500;
  }
}

.spoiler-toggle{
  display: flex;
  align-items: center;
  font-weight: 500;
  transition: all .3s;
  span{
    font-size: 1em;
    line-height: 140%;
  }
  img{
    width: .75em;
    display: block;
    flex-shrink: 0;
    margin-left: 1.25em;
    transition: all .3s;
  }
  ._view2{
    display: none;
  }
  &:hover{
    color: $blue;
    cursor: pointer;
  }
  &._active{
    color: $blue;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
    img{
      transform: rotate(180deg);
    }
  }
}

.user-info{
  padding-top: .9375em;
  margin-top: .9375em;
  border-top: $border2;
  display: none;
  &__groups{
    margin-top: .9375em;
    @include grid100gap(.9375em);
  }
  &__group{
    @include grid100gap(.3125em);
  }
}

.pay-options{
  display: none;
  &__nav{
    margin-top: 1.875em;
    display: flex;
  }
  &__btn{
    padding: 0 .9375em;
    height: 3.25em;
    font-weight: 600;
    font-family: $font1;
  }
}

.shipment{
  margin-top: .625em;
  display: none;
  .order-table{
    margin: 0;
  }
  @media screen and (min-width: 993px){
    td,th{
      &:last-child{
        padding-right: 6em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .order-card{
    &__top{
      padding-left: 2.25em;
      padding-right: 2.25em;
    }
    &__bottom{
      padding: 2.25em 2.25em 1.5em;
      display: grid;
      grid-template-columns: 1.4fr 1.2fr 1fr;
      gap: .9375em;
    }
    &__btn{
      padding: 0;
      margin: 0;
    }
  }

  .order-info{
    padding-left: 2.25em;
    padding-right: 2.25em;
  }

  .order-item{
    &__back{
      padding: 0;
      border: none;
      margin-top: .625em;
    }
    &__subtitle{
      margin-top: .625em;
      padding: 0;
    }
  }

  .order-detail{
    margin-top: 2em;
    &__wrap{
      grid-template-columns: .9fr 1fr .7fr;
      grid-row: 1.5625em;
    }
    &__nav{
      grid-row: 2/3;
      grid-column: 2/4;
      grid-template-columns: 1fr 1fr;
      max-width: 23.5625em;
    }
    &__more{
      align-self: center;
    }
  }

  .parameters{
    margin-top: 2em;
  }

  .parameter-card{
    &__wrap{
      align-items: center;
    }
    &__badges{
      padding: 0;
    }
  }

  .order-table{
    th,
    td{
      width: unset;
      padding-left: 1em;
      &:first-child{
        padding-left: 1.5em;
        width: 20.375em;
      }
      &:last-child{
        padding-right: 1em;
      }
    }
    &__item{
      grid-template-columns: 10.625em 10.625em;
    }
  }

  .pay-options{
    .pay-ways{
      &__grid{
        width: calc(100% + 1.25em);
      }
    }
    .pay-way{
      margin: .625em .625em 0 0;
    }
    &__nav{
      margin-top: .625em;
    }
  }

  .shipment{
    margin-top: .625em;
    .order-table{
      margin: 0;
    }
    th,td{
      padding: .625em;
    }
    @media screen and (min-width: 577px){
      td,th{
        &:first-child{
          width: auto;
        }
        &:last-child{
          padding-right: 4em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .orders{
    &__badge{
      padding: .625em 1.25em;
      justify-content: center;
      span{
        font-size: 1em;
      }
    }
    &__items{
      margin-top: 2.125em;
      gap: 1.875em;
    }
  }

  .order-card{
    border: none;
    &__top{
      padding: 0;
      min-height: unset;
      display: block;
      max-width: 13.125em;
    }
    &__bottom{
      margin-top: 1.875em;
      padding: 0;
      border: none;
      grid-template-columns: 100%;
      gap: .875em;
    }
  }

  .order-info,
  .parameter-card{
    .filter-select{
      .nice-select span{
        font-size: 1em;
        font-weight: 500;
      }
    }
  }

  .order-info{
    padding: 0;
    margin-top: .6875em;
    border: none;
    &__wrap{
      flex-direction: column;
      align-items: flex-start;
    }
    &__badge{
      margin: .6875em 0 0;
    }
    &__sum{
      max-width: 13.125em;
    }
    &__status{
      flex-direction: column;
      align-items: flex-start;
      &-badge{
        margin: .5em 0 0;
      }
    }
  }

  .order-item{
    &__subtitle,
    &__back{
      margin-top: .5em;
    }
  }

  .order-detail{
    padding: 1.375em 1.5625em;
    &__wrap{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__nav{
      grid-template-columns: 100%;
      order: 2;
      grid-row: unset;
      grid-column: unset;
      margin-top: .625em;
    }
    &__more{
      margin-top: 1.125em;
    }
  }

  .parameters{
    padding: 1.375em 1.5625em;
    border: $border2;
    border-radius: .5em;
  }

  .parameter-card{
    margin-top: 1.25em;
    padding: 0;
    border: none;
    &__content{
      grid-template-columns: 100%;
      margin-top: .625em;
      padding: 0;
      border: none;
      gap: .625em;
    }
    &__img{
      width: 8em;
      padding: 1.25em .5625em;
    }
    &__wrap{
      grid-template-columns: 100%;
      gap: 1.75em;
    }
    &__text{
      gap: .625em;
    }
    &__sum{
      &-title{
        font-size: .875em;
        color: $grey2;
      }
      &-num{
        display: block;
        font-weight: 500;
      }
      p{
        span{
          display: block;
        }
      }
    }
    .order-info__badge{
      height: 3.25em;
      color: #AEAEAE;
    }
    .filter-select{
      margin-top: 1.25em;
    }
  }

  .order-contain{
    margin-top: 1.25em;
  }

  .order-table{
    display: block;
    border: none;
    margin-top: 1.25em;
    thead{
      display: none;
    }
    tbody{
      display: block;
    }
    tr{
      @include grid100gap(.5em)
    }
    td{
      text-align: left;
      &:first-child{
        width: 100%;
      }
    }
    td{
      border: none;
      padding: 0 !important;
    }
    &__bottom{
      padding: 0;
      margin-top: 2.125em;
    }
    &__item{
      gap: 1em;
      grid-template-columns: calc(100% - 7.25em) 6.25em;
    }
  }

  .order-row{
    display: block;
    padding: .625em .625em .625em 7.5em;
    position: relative;
    border-bottom: $border2;
    &__img{
      position: absolute;
      top: 50%;
      left: .625em;
      width: 6.25em;
      transform: translateY(-50%);
    }
    &__info{
      display: block;
    }
    &__title{
      font-size: .875em;
      font-weight: 600;
    }
    &__prop{
      &-title,
      &-desc{
        display: inline-block;
        font-size: .875em;
        line-height: 140%;
      }
    }
    ._count{
      font-weight: 500;
    }
    ._sum{
      font-weight: 600;
    }
  }

  .user-info{
    padding: 0;
    border: none;
    margin-top: 1.25em;
    &__groups{
      margin-top: .625em;
      gap: .625em;
    }
    &__group{
      gap: .625em;
    }
  }

  .pay-options {
    margin-top: 1.5625em;
    .pay-ways {
      &__grid {
        width: 100%;
      }
    }
    .pay-way{
      margin: 0;
      label{
        padding: 0 .625em;
        min-height: 2.8125em;
      }
    }
    &__btn{
      font-weight: 500;
      padding: 0;
      width: 100%;
      height: 2.625em;
    }
  }

  .shipment{
    .order-table{
      margin-top: 1.75em;
      border: $border2;
    }
    .order-row{
      padding-left: 4.25em;
      gap: 0;
      &__img{
        width: 3em;
        top: .625em;
        transform: unset;
      }
      &__title{
        font-weight: 400;
        font-size: 1em;
      }
      ._count{
        font-weight: 400;
      }
      &__prop{
        &-title{
          display: none;
        }
        &-desc{
          font-size: 1em;
        }
      }
    }
  }
}
