.cart{
  flex-grow: 1;
  padding: 1.875em 0 5.625em;
  background: $lightGrey;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 19.3125em) 17.3125em;
    gap: 2em;
  }
  &__items{
    padding: .5em 0;
    border: $border2;
    border-radius: .5em;
  }
  &__nav{
    margin-top: .75em;
    display: flex;
    justify-content: center;
  }
  &__refresh{
    font-family: $font2;
    font-size: 1em;
    line-height: 140%;
    color: $darkGrey;
    &:hover{
      color: $blue;
    }
  }
}

.cart-card{
  padding: .875em 1.5625em;
  display: grid;
  align-items: center;
  grid-template-columns: 8.0625em calc(100% - 35.8125em ) 7.25em 7.5em 1em;
  gap: 3em;
  &:not(:last-child){
    border-bottom: $border2;
  }
  &__img{
    width: 100%;
    height: 9.375em;
    flex-shrink: 0;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__badges{
    display: flex;
    margin-top: -.3125em;
    margin-bottom: .625em;
  }
  &__calc{
    position: relative;
    &-text{
      position: absolute;
      top: 100%;
      width: 100%;
      text-align: center;
      margin-top: 1em;
    }
  }
  &__nums{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    .badge{
      margin: 0;
    }
  }
  &__prices{
    margin-top: .375em;
    @include grid100gap(.375em);
    text-align: right;
    &-old{
      text-decoration-line: line-through;
    }
  }
  &__delete{
    svg{
      width: 1em;
      height: 1em;
      fill: none;
      stroke: $grey;
    }
    &:hover{
      svg{
        stroke: $red;
      }
    }
  }
}

.cart-sticky{
  position: sticky;
  top: 7.5em;
}

.cart-sum{
  padding: 1.5em 1.25em;
  border: $border2;
  border-radius: .5em;
  @include grid100gap(1.25em);
  &__form{
    @include grid100gap(1.25em);
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    font-family: $font2;
    height: 2.375em;
    span{
      font-size: .875em;
    }
  }
  &__desc,
  &__props{
    @include grid100gap(.625em);
  }
  &__prop{
    display: flex;
    justify-content: space-between;
    position: relative;
    &-title,
    &-desc{
      position: relative;
      z-index: 2;
      background-color: #fff;
    }
    &-title{
      padding-right: .25em;
    }
    &-desc{
      padding-left: .25em;
    }
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: .375em;
      width: 100%;
      height: 1px;
      border-bottom: 1px dotted #DADADA;
    }
  }
}

.cart-hint{
  display: grid;
  grid-template-columns: auto 1.875em;
  gap: .625em;
  width: calc(100 - 2.5em);
  padding: .8125em .625em;
  background: #E9E9E9;
  border-radius: .5em;
  &__content{
    @include grid100gap(.5em);
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding-bottom: 1.25em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .cart-card{
    padding-left: .625em;
    padding-right: .625em;
    grid-template-columns: 8.0625em calc(100% - 33.6875em ) 7.25em 7.5em .875em;
    gap: 2.5em;
    &__delete{
      svg{
        width: .875em;
      }
    }
  }

  .cart-sum{
    @media screen and (min-width: 577px){
      grid-template-columns: repeat(2,calc(50% - .625em));
      align-items: flex-start;
      &__btn{
        height: 2.75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart{
    &__item{
      padding-top: .3125em;
      padding-bottom: .3125em;
    }
  }

  .cart-card{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    grid-template-columns: 6.125em calc(100% - 8.25em) .875em;
    gap: 1.25em .625em;
    &__img{
      height: 7.125em;
    }
    &__action{
      grid-column: 3/4;
      grid-row: 1/2;
    }
    &__nums{
      grid-column: 2/4;
      grid-row: 2/3;
    }
    &__badges{
      margin-bottom: .5em;
      .badge{
        padding: 0 .5em;
        height: 1.4375em;
        border-radius: .54em;
        span{
          font-size: .6875em  ;
        }
      }
    }
    &__title{
      font-size: .875em;
    }
    &__nums{
      .badge{
        padding: 0 .5625em;
        height: 1.6875em;
        border-radius: .58em;
        span{
          font-size: .75em;
        }
      }
    }
    &__calc{
      width: calc(100% + .625em);
      &-text{
        font-size: .75em;
        margin-top: .9em;
      }
    }
    &__prices{
      &-new{
        font-size: 1.125em;
      }
      &-old{
        font-size: .75em;
      }
    }
  }

  .cart-sum{
    &__top{
      font-size: 1.25em;
    }
    &__desc{
      font-weight: 500;
    }
  }
}
