.catalog-item{
  padding: 2.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: 26em calc(100% - 28.5em);
    gap: 2.5em;
  }
  &__img{
    display: block;
    padding-bottom: 106%;
    position: relative;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
      @include centerAbsolute;
    }
  }
}

.product-review{
  display: flex;
  align-items: center;
  &__rating{
    display: flex;
    align-items: center;
    margin-right: 1.6875em;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .4375em;
    }
  }
}

.product-info{
  display: grid;
  grid-template-columns: calc(100% - 21em) 17.6875em;
  gap: 3.125em;
  margin-top: 1.875em;
}

.props{
  &__items{
    margin-top: .875em;
    @include grid100gap(.652em);
  }
}

.product-desc{
  margin-top: 3em;
  &__text{
    p{
      line-height: inherit;
    }
  }
  &__nav{
    margin-top: .375em;
  }
  &__btn{
    font-size: 1em;
    line-height: 140%;
    text-decoration-line: underline;
    color: $darkBlue;
  }
}

.product-price{
  padding: .625em 1.0625em 1.75em;
  border-radius: .5em;
  border: $border2;
  &__new{
    font-size: 1.25em;
    line-height: 140%;
    font-weight: 500;
  }
  &__old{
    font-size: .875em;
    line-height: 140%;
    color: $grey2;
    text-decoration: line-through;
  }
  &__nav{
    margin-top: 1.4375em;
    @include grid100gap(.75em);
  }
  &__btn{
    height: 2.75em;
    span{
      font-size: .875em;
    }
  }
}

.product-has{
  padding: 1.0625em 1.25em;
  border-radius: .5em;
  border: $border2;
  display: flex;
  align-items: center;
  margin-top: 1.25em;
  &__icon{
    width: .75em;
    height: .75em;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: .4375em;
    background-color: $blue;
  }
  &__text{
    font-size: .875em;
    line-height: 140%;
    color: $green;
    font-weight: 500;
  }
}

.product-detail{
  margin-top: .9375em;
  position: relative;
  ._anchor{
    content: '';
    position: absolute;
    width: 1px;
    height: 1px;
    bottom: 100%;
    transform: translateY(-5em);
  }
  &__tabs{
    display: flex;
    border-bottom: $border2;
  }
  &__content{
    margin-top: 1.875em;
  }
}

.product-text{
  &__nav{
    display: none;
  }
}

.text{
  h3{
    font-weight: 600;
    &:not(:last-child){
      margin-bottom: 1.6em;
    }
  }
  p,
  ul{
    &:not(:last-child){
      margin-bottom: 1em;
    }
    a{
      text-decoration: underline;
    }
  }
  ul{
    padding-left: .625em;
    li{
      position: relative;
      padding-left: 1em;
      &:before{
        content: '';
        position: absolute;
        top: .53125em;
        left: 0;
        width: .3125em;
        height: .3125em;
        border-radius: 50%;
        background-color: $black;
      }
    }
  }
  ol{
    padding-left: .375em;
    counter-reset: list;
    li{
      counter-increment: list;
      &:before{
        content: counter(list) '.';
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
        min-width: 1.25em;
        display: inline-block;
      }
    }
  }
  hr{
    display: block;
    height: 1em;
    border: none;
  }
}

.service{
  padding: 2.25em 0 4.0625em;
  background-color: $lightGrey;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 32em) 30.125em;
    gap: 1.875em;
    position: relative;
  }
  &__img{
    position: absolute;
    height: 100%;
    width: 30.125em;
    top: 0;
    right: 0;
    img{
      width: 100%;
      max-height: 100%;
      object-fit: cover;
      border-radius: .5em;
      display: block;
    }
  }
  .text{
    margin-top: 1.75em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    padding: 0 3.125em;
    margin-right: 1.875em;
  }
}

.history,
.related-service{
  &__slider{
    margin-top: 2.5em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-self: stretch;
    }
    &-slide{
      width: 25%;
      height: auto;
      padding-right: 1.875em;
    }
  }
}

.related-service{
  padding: 2.5em 0;
  overflow: hidden;
  &._gray-bg{
    background-color: $lightGrey;
  }

}

.service-card2{
  background: #FFFFFF;
  border-radius: .5em;
  border: 1px solid $tabGrey;
  display: flex;
  flex-direction: column;
  height: 100%;
  &__img{
    display: block;
    @include imgCover(100%);
    img{
      border-radius: .5em .5em 0 0;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 2em 1em 1.5em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
  }
  &__btn{
    padding: 0 1em;
  }
}

.product-collection{
  overflow: hidden;
  padding: 2.5em 0;
  background: $lightGrey;
  &._white{
    background: transparent;
  }
  .offer__slider{
    margin-top: 2.5em;
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(5,calc(20% - 6em / 5));
        gap: 1.5em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.product-nav{
  padding: 1.375em 0 3.125em;
  &__wrap{
    display: flex;
  }
  &__link{
    display: flex;
    align-items: center;
    font-family: $font2;
    &:hover{
      svg{
        fill: $blue;
      }
    }
    svg{
      width: 1.625em;
      height: 1.375em;
      margin-right: .375em;
      fill: #4A4A4A;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
  }
}

.feedback{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 17.3125em - 1.875em) 17.3125em;
    gap: 1.875em;
    margin-top: 1.875em;
  }
  &__add-new{
    display: none;
  }
}

.feedback-nav,
.feedback__hint,
.feedback-form{
  border: $border2;
  border-radius: .5em;
  padding: 1.25em;
}

.feedback-nav{
  padding: 1.25em 2.375em;
  @include grid100gap(1.25em);
  text-align: center;
  &__btn{
    height: 2.375em;
    span{
      font-size: .875em;
    }
  }
}

.feedback-form{
  margin-bottom: 1.875em;
  @include grid100gap(.625em);
  .fg{
    label{
      color: $black;
      &:after{
        color: $red;
        margin-left: .25em;
      }
    }
  }
  &__rating{
    display: flex;
    padding-bottom: .625em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    height: 2.375em;
    span{
      font-size: .875em;
    }
  }
}

.feedback-files{
  &__fields{
    @include grid100gap(.625em);
  }
  &__nav{
    margin-top: .625em;
  }
  &__btn{
    font-size: .75em;
    line-height: 140%;
    color: $blue;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
}

.payment,
.delivery{
  .text{
    margin-top: 3.125em;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding: 0 0 3.3125em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
    &__col{
      &:nth-child(1){
        width: 100%;
        max-width: 26em;
        margin: 0 auto;
      }
    }
  }

  .product-detail{
    margin-top: 6.6875em;
  }

  .service{
    padding: 1.875em 0 3.25em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      position: static;
      margin-top: 1.875em;
      height: 21.375em;
      width: 100%;
      img{
        height: 100%;
      }
    }
    &__nav{
      margin-top: 1.875em;
    }
  }

  .related-service{
    padding: 1.875em 0 3.625em;
    &__slider{
      margin-top: 1.875em;
    }
    .swiper{
      &-slide{
        width: 19.1875em;
      }
    }
  }

  .service-card2{
    &__img{
      width: calc(100% + 2px);
      margin: -1px 0 0 -1px;
    }
  }

  .product-collection{
    padding: 1.875em 0;
    .offer__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 1.5em;
    }
    .swiper{
      &-container{
        width: calc(100% + 1.5em);
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: calc(100% / 3);
        padding-right: 1.5em;
      }
    }
  }

  .product-nav{
    padding-bottom: 1.375em;
  }

  .feedback{
    &__grid{
      grid-template-columns: 100%;
      gap: 3.125em;
    }
    &__sidebar{
      order: -1;
    }
  }

  .feedback-nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    border: none;
    gap: unset;
    &__btn{
      padding: 0 1.5em;
    }
  }

  .feedback-form{
    padding: 0;
    border: none;
  }

  .payment,
  .delivery{
    .text{
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    padding-bottom: 5.125em;
    &__grid{
      gap: 1.25em;
    }
    &__img{
      position: static;
      padding: 0;
      img{
        transform: unset;
        position: static;
        width: 100%;
        max-height: unset;
      }
    }
  }

  .product-info{
    grid-template-columns: 100%;
    gap: 1.25em;
  }

  .product-desc{
    margin-top: .625em;
  }

  .props{
    &__items{
      gap: .3125em;
    }
  }

  .product-price{
    padding-bottom: 1.25em;
  }

  .product-detail{
    margin-top: .4375em;
    &__top{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        left: 0;
        bottom: 1px;
        border-bottom: $border2;
      }
    }
    &__tabs{
      overflow-x: auto;
      width: calc(100% + 1.25em);
      margin-left: -.625em;
      padding: 0 .625em 1.5px;
      position: relative;
      border: none;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &__content{
      margin-top: 1.125em;
    }
  }

  .text{
    h3{
      &:not(:last-child){
        margin-bottom: 1em;
      }
    }
    hr{
      height: .25em;
    }
  }

  .product-text{
    &:not(._active){
      .text{
        height: 24.375em;
        overflow: hidden;
      }
    }
    &._active{
      .product-text__toggle{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &__nav{
      margin-top: 1.125em;
      display: flex;
    }
    &__toggle{
      text-decoration-line: underline;
      color: $darkBlue;
      font-size: 1em;
      line-height: 140%;
      ._view2{
        display: none;
      }
      &:hover{
        text-decoration: none;
        color: $blue;
      }
    }
  }

  .service{
    padding-bottom: 2.375em;
    &__nav{
      @include grid100gap(.875em);
    }
    &__btn{
      margin: 0;
      padding: 0;
      height: 2.75em;
    }
    &__img{
      height: 17.5em;
    }
  }

  .related-service{
    padding: 1.625em 0 4.375em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: 100%;
        padding-right: 1.25em;
      }
    }
  }

  .product-collection{
    padding: 1.5625em 0;
    .offer__grid{
      margin-top: .6875em;
      grid-template-columns: repeat(2,calc(50% - .15625em));
      gap: .3125em;
    }
    .offer__slider{
      margin-top: .6875em;
    }
    .swiper{
      &-container{
        width: calc(100% + .3125em);
      }
      &-slide{
        width: 50%;
        padding-right: .3125em;
      }
    }
  }

  .feedback-nav{
    flex-direction: column;
    align-items: flex-start;
    &__btn{
      margin: 1.25em 0 0;
    }
  }
}
