//grid styles
._grid{
  display: grid;
  grid-template-columns: repeat(12,calc(100% / 12 - 11 * 1.875em / 12));
  gap: 1.875em;
  grid-auto-flow: dense;
  &._center{
    justify-content: center;
  }
  .col{
    grid-column: span 1;
    width: 100%;
  }
  .col1{
    grid-column: span 1;
  }
  .col2{
    grid-column: span 2;
  }
  .col3{
    grid-column: span 3;
  }
  .col4{
    grid-column: span 4;
  }
  .col5{
    grid-column: span 5;
  }
  .col6{
    grid-column: span 6;
  }
  .col7{
    grid-column: span 7;
  }
  .col8{
    grid-column: span 8;
  }
  .col9{
    grid-column: span 9;
  }
  .col10{
    grid-column: span 10;
  }
  .col11{
    grid-column: span 11;
  }
  .col12{
    grid-column: span 12;
  }
}

._test-gray{
  background-color: $grey2;
  height: 10em;
}


@media screen and (max-width: 992px){
  ._grid{
    .col-tablet1{
      grid-column: span 1;
    }
    .col-tablet2{
      grid-column: span 2;
    }
    .col-tablet3{
      grid-column: span 3;
    }
    .col-tablet4{
      grid-column: span 4;
    }
    .col-tablet5{
      grid-column: span 5;
    }
    .col-tablet6{
      grid-column: span 6;
    }
    .col-tablet7{
      grid-column: span 7;
    }
    .col-tablet8{
      grid-column: span 8;
    }
    .col-tablet9{
      grid-column: span 9;
    }
    .col-tablet10{
      grid-column: span 10;
    }
    .col-tablet11{
      grid-column: span 11;
    }
    .col-tablet12{
      grid-column: span 12;
    }
  }
}


@media screen and (max-width: 576px){
  ._grid{
    grid-template-columns: repeat(12,calc(100% / 12 - 11em / 12));
    gap: 1em;
    .col-mob1{
      grid-column: span 1;
    }
    .col-mob2{
      grid-column: span 2;
    }
    .col-mob3{
      grid-column: span 3;
    }
    .col-mob4{
      grid-column: span 4;
    }
    .col-mob5{
      grid-column: span 5;
    }
    .col-mob6{
      grid-column: span 6;
    }
    .col-mob7{
      grid-column: span 7;
    }
    .col-mob8{
      grid-column: span 8;
    }
    .col-mob9{
      grid-column: span 9;
    }
    .col-mob10{
      grid-column: span 10;
    }
    .col-mob11{
      grid-column: span 11;
    }
    .col-mob12{
      grid-column: span 12;
    }
  }
}
//end grid styles
