.docs{
  padding-bottom: 5em;
}

.docs-group{
  &:not(:last-child){
    margin-bottom: 5em;
  }
  &__items{
    border: $border2;
    border-radius: .5em;
    margin-top: 3.5em;
  }
}


.doc-card{
  padding: 1.25em 2.5em;
  display: grid;
  grid-template-columns: 6.125em calc(100% - 12.625em) 1.5em;
  gap: 2.5em;
  font-family: $font2;
  &:not(:last-child){
    border-bottom: $border2;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__download{
    svg{
      width: 1.5em;
      height: 1.5em;
      fill: none;
      stroke: $grey;
    }
    &:hover{
      svg{
        stroke: $blue;
      }
    }
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__title{
    font-size: 1.125em;
    line-height: 140%;
    font-weight: 500;
  }
  &__size{
    font-size: .75em;
    line-height: 140%;
    color: $grey2;
  }
}


@media screen and (max-width: 992px){
  .docs{
    padding-bottom: 3.625em;
  }

  .docs-group{
    &:not(:last-child){
      margin-bottom: 2.125em;
    }
    &__items{
      margin-top: 1.5em;
    }
  }

  .doc-card{
    padding: 1.25em;
    grid-template-columns: 4.625em calc(100% - 12.125em) 1.5em;
    gap: 3em;
  }
}


@media screen and (max-width: 576px){
  .docs{
    padding-bottom: 1.5em;
  }

  .docs-group{
    &:not(:last-child){
      margin-bottom: 1.375em;
    }
    &__items{
      padding: 0;
      border: none;
      @include grid100gap(.625em);
    }
  }

  .doc-card{
    min-height: 5.4375em;
    padding: 0 .625em 0 0;
    grid-template-columns: 3.125em calc(100% - 6.625em) 1.5em;
    gap: 1em;
    border: none !important;
    &__desc{
      display: none;
    }
  }
}
