.lk{
  padding-bottom: 2.5em;
  &__grid{
    display: grid;
    grid-template-columns: 17.5em calc(100% - 17.5em - 1.875em);
    gap: 1.875em;
  }
}

.lk-nav{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    font-family: $font2;
    font-size: 1em;
    line-height: 140%;
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-bottom: .9375em;
    }
    &._active{
      padding: .125em .28125em;
      font-weight: 500;
      background-color: $tabGrey;
      border-radius: .5em;
      pointer-events: none;
    }
  }
}

.my-cabinet{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
  gap: 1.875em;
  &__link{
    min-height: 11.25em;
    background: #F9F9FB;
    border: $border2;
    border-radius: .5em;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  &__img{
    height: 3.125em;
    margin-bottom: 1.25em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__title{
    font-family: $font2;
    font-size: 1em;
    line-height: 140%;
  }
}

.lk-form{
  padding: 1.875em 2.8125em;
  border-radius: .5em;
  border: $border2;
  max-width: 44.5em;
  box-shadow: 0 0 .625em rgba(123, 152, 196, 0.3);
  &__fields{
    margin-top: 1.5625em;
    @include grid100gap(.9375em);
  }
  &__nav{
    display: flex;
    margin-top: 1.5625em;
  }
  &__btn{
    padding: 0 2.0625em;
  }
  &__hint{
    font-size: .875em;
    line-height: 140%;
    font-family: $font2;
    margin-top: 1.6em;
    a{
      color: $darkGrey;
      &:hover{
        color: $blue;
      }
    }
  }
}

.req{
  padding: 2.875em 0;
  .lk-form{
    margin: 0 auto;
    max-width: 36.5625em;
    padding: 0;
    box-shadow: none;
    border: none;
    &__fields{
      margin: 0;
      gap: 1.25em;
    }
    &__btn{
      width: 100%;
    }
    &__hint{
      margin-top: 1.78em;
      text-align: center;
    }
  }
}

.login-socials{
  margin-top: 6.25em;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.8125em / 4));
    gap: .9375em;
    margin-top: 1.875em;
  }
  &__btn{
    @include center;
    height: 3.375em;
    border-radius: .5em;
    border: $border2;
    &:hover{
      border-color: $blue;
    }
    svg{
      width: 1.5em;
      height: 1.5em;
    }
    img{
      width: 1.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .lk{
    padding-bottom: 3.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
  }

  .lk-nav{
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: -.3125em;
    &__link{
      margin: .3125em .9375em 0 0 !important;
    }
  }

  .my-cabinet{
    grid-template-columns: repeat(2,calc(50% - .9375em));
    max-width: 36.375em;
  }

  .lk-form{
    margin: 0 auto;
    max-width: 36.25em;
    padding: 2.5em 2.5em 2.875em;
    @media screen and (min-width: 577px){
      &__title{
        font-weight: 600;
        font-family: $font1;
        text-align: center;
        font-size: 2em;
      }
      &__fields{
        gap: 2.375em;
      }
      &__nav{
        margin-top: 2.8125em;
      }
      &__btn{
        width: 100%;
      }
    }
  }

  .req{
    padding: 2.125em 0 4.875em;
  }
}


@media screen and (max-width: 576px){
  .lk{
    padding-bottom: 1.5em;
    &__grid{
      gap: 1.5em;
    }
  }

  .lk-nav{
    &__link{
      span{
        font-size: .875em;
      }
      &._active{
        padding: .125em .6875em;
      }
    }
  }

  .my-cabinet{
    grid-template-columns: 100%;
    gap: .625em;
  }

  .lk-form{
    padding: 1.25em 1.25em 1.625em;
    &__fields{
      margin-top: .625em;
      gap: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
    &__hint{
      margin-top: .71em;
    }
  }

  .req{
    padding: 1em 0 3em;
    .lk-form{
      &__hint{
        margin-top: 1.42em;
        padding: 0 1em;
      }
    }
  }

  .login-socials{
    margin-top: 2.875em;
    &__title{
      font-size: .875em;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
      gap: .625em;
      margin-top: 1.25em;
    }
    &__btn{
      @include center;
      height: 2.0625em;
      border-radius: .3125em;
      svg{
        width: .9375em;
        height: .9375em;
      }
      img{
        width: .9375em;
      }
    }
  }
}
