.services-page{
  padding-bottom: 7.1875em;
  &__grid{
    display: grid;
    grid-template-columns: 17.375em calc(100% - 17.375em - 1.875em);
    gap: 1.875em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    &:not(:last-child){
      margin-bottom: .9375em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__items{
    margin-top: 2.5em;
  }
}

.service-top{
  &__img{
    width: 100%;
    border-radius: .5em;
    display: block;
    margin-top: 3.4375em;
  }
}

.service-order{
  padding: 1.25em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.875em 6.375em;
    background-color: $blue;
    border-radius: .5em;
    color: #fff;
  }
  &__content{
    @include grid100gap(.9375em);
  }
  &__nav{
    flex-shrink: 0;
    margin-left: 4em;
  }
  &__btn{
    width: 17.375em;
    height: 3.25em;
    font-weight: 600;
    color: $black;
  }
}

.service-detail{
  padding: 1.25em 0 1.875em;
}

.tariffs{
  padding: 2.5em 0 2.125em;
  background-color: $lightGrey;
  .text{
    margin-top: 2.5em;
  }
  &__nav{
    display: flex;
    margin-top: 1.75em;
  }
  &__btn{
    height: 4.375em;
    padding: 0 2.125em;
    font-weight: 600;
  }
  &__slider{
    margin-top: 2.5em;
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        flex-wrap: wrap;
        display: flex;
        justify-content: center;
        transform: unset !important;
      }
      &-slide{
        margin: 2.5em 1.1875em 0;
        width: 13.75em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .services-page{
    padding-bottom: 4.375em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__sidebar{
      display: none;
    }
    &__items{
      margin-top: 4em;
      grid-template-columns: repeat(2,calc(50% - 1.375em));
      gap: 2.75em;
    }
  }

  .service-top{
    position: relative;
    padding: 13.25em 0 1.75em;
    color: #FFFFFF;
    &:before{
      content: '';
      position: absolute;
      inset: 0;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
    }
    &__img{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      object-fit: cover;
      z-index: -1;
      margin: 0;
      border-radius: 0;
    }
    .page-top__inner{
      gap: 0;
      position: relative;
      z-index: 2;
    }
    .breadcrumbs{
      &__item{
        color: $lightGrey;
        &:not(:first-child):before{
          background-image: url(../img/bread-arrow2.svg);
        }
      }
    }
  }

  .service-order{
    margin: 1.375em 0 0;
    padding: 0;
    &__inner{
      padding: 1.5625em 1.75em;
    }
    &__content{
      gap: .4375em;
    }
    &__btn{
      width: 12.5em;
    }
    .text32{
      font-size: 1.5em;
    }
  }

  .service-detail{
    padding-top: 2.375em;
  }

  .tariffs{
    overflow: hidden;
    .text{
      margin-top: 4.625em;
    }
    &__slider{
      margin-top: 2.125em;
    }
    .swiper{
      &-container{
        width: calc(100% + 1.5625em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: .5625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .services-page{
    padding-bottom: 4.6875em;
    &__items{
      margin-top: 1.25em;
      gap: 1.25em;
      grid-template-columns: 100%;
    }
    .service-card2{
      &__nav{
        padding-top: 1.25em;
      }
    }
  }

  .service-top{
    padding: 14.375em 0 .75em;
  }

  .service-order{
    &__inner{
      display: block;
      padding: .75em 1.25em 1.5625em;
    }
    &__nav{
      margin: 1.25em 0 0;
    }
  }

  .service-detail{
    padding: 1.875em 0 1.25em;
  }

  .tariffs{
    padding: 1.5em 0 1.875em;
    .text{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
      padding: 0 .875em;
      white-space: pre-wrap;
      text-align: center;
      height: 3.75em;
    }
    &__slider{
      margin-top: 1em;
    }
    .swiper{
      &-container{
        width: calc(100% + .375em);
      }
      &-slide{
        width: 50%;
        padding-right: .375em;
      }
    }
  }
}
