.product-modal,
.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    &__bg {
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &--active {
        opacity: 1;
        pointer-events: unset;
        .product-modal__bg,
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    &__bg {
        background: rgba(0, 0, 0, 0.2);
    }
    &__dialog {
        background: #fff;
        width: 36.25em;
        max-width: calc(100% - 1em);
        overflow-y: auto;
        max-height: calc(100 * var(--vh));
        padding: 2.5em;
        border-radius: .5em;
        position: relative;
        z-index: 3;
    }

    &__close {
        position: absolute;
        top: 0;
        right: 0;
        width: 3em;
        height: 3em;
        svg {
            width: 100%;
            height: 100%;
            fill: #000000;
        }
        &:hover{
            svg{
                fill: $blue;
            }
        }
    }
    &__title{
        text-align: center;
    }
    &__form{
        margin-top: 1.875em;
        @include grid100gap(1.25em);
        .fg{
            @media screen and (min-width: 577px){
                textarea,
                input{
                    font-size: 1em;
                    height: 3.75em;
                    border-radius: .5em;
                    padding: 0 1em;
                }
                textarea{
                    height: 9.375em;
                }
            }
        }
    }
    &__nav{
        margin-top: -.625em;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .m-check{
            label{
                font-family: $font2;
                align-items: center;
                &:before{
                    width: 1.5em;
                    height: 1.5em;
                    margin-right: 1.25em;
                    transform: unset;
                }
                span{
                    font-size: 1em;
                }
            }
        }
    }
    &__nav2{
        margin-top: .625em;
        @include grid100gap(1.5em);
    }
    &__btn{
        width: 100%;
        font-weight: 600;
    }
    &__confirm{
        margin-top: 1.5em;
        font-family: $font2;
        a{
            color: $blue;
        }
    }
    .login-socials{
        margin-top: 1.5em;
        &__title{
            font-size: 1em;
        }
        &__grid{
            margin-top: 1.25em;
        }
    }
    &__logo{
        display: none;
    }
}

.service-modal{
    .modal__title{
        text-align: left;
    }
}

.product-modal{
    &__bg {
        background: rgba(0, 0, 0, 0.4);
    }
    &__slider{
        background: #FFFFFF;
        box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.25);
        border-radius: .5em;
        padding: 1.25em;
        width: 49.375em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        max-height: calc(100 * var(--vh));
        overflow-y: auto;
    }
    .swiper{
        &-container{
            overflow: hidden;
        }
        &-button{
            &:before{
                background: #F9F9FB;
                opacity: 1;
            }
            svg{
                stroke: #282828;
            }
            &-prev{
                left: -2.8125em;
            }
            &-next{
                right: -2.8125em;
            }
        }
    }
    &__detail{
        grid-column: 1/-1;
        font-family: $font2;
        height: 2.5em;
        span{
            font-size: .875em;
        }
    }
}

.product{
    display: grid;
    grid-template-columns: 20.125em calc(100% - 22.125em);
    gap: 2em;
    &__img{
        @include center;
        img{
            width: 100%;
        }
    }
    .product-review{
        margin-top: .75em;
    }
    .product-price{
        padding: 0;
        border: none;
        margin-top: .625em;
        &__nav{
            margin-top: .625em;
        }
        &__top{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }
    .product-has{
        margin-top: .625em;
        padding: 0;
        border: none;
        font-family: $font2;
    }
    .props{
        margin-top: 1em;
    }
    .product-desc{
        margin-top: 1.6875em;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            height: calc(100 * var(--vh));
            max-width: 100%;
            border-radius: 0;
            padding: 0 1.25em 3em;
        }
        &__header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: calc(100% + .5em);
            margin-left: -.25em;
            margin-bottom: 3.3125em;
        }
        &__logo{
            width: 9.625em;
            min-height: 4em;
            display: flex;
            align-items: center;
            img{
                width: 100%;
            }
        }
        &__close{
            position: static;
        }
        &__form{
            gap: .75em;
        }
        &__nav{
            .m-check{
                label{
                    &:before{
                        width: .875em;
                        height: .875em;
                        margin-right: .75em;
                        border: 0.602134px solid #969696;
                    }
                    span{
                        font-size: .625em;
                    }
                }
            }
        }
        &__forgot{
            font-size: .625em;
        }
        &__nav2{
            margin-top: .5em;
            gap: .875em;
        }
        &__btn{
            height: 2em;
            span{
                font-size: .75em;
            }
        }
        &__confirm{
            margin-top: .75em;
            .text14{
                font-size: .5625em;
            }
        }
        .login-socials{
            &__title{
                font-size: .625em;
            }
            &__grid{
                margin-top: .75em;
            }
        }
    }

    .service-modal{
        .modal__dialog{
            padding-top: 3.125em;
        }
        .modal__close{
            top: .625em;
            right: .625em;
            position: absolute;
        }
    }
}
