// header styles
.logo{
  width: 13.75em;
  flex-shrink: 0;
  color: $darkBlue;
  white-space: nowrap;
  &__img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 1.25em 0 1.5625em;
  background-color: #fff;
  z-index: 10;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: all .3s;
  &__inner{
    position: relative;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__call{
    display: flex;
    align-items: center;
    margin-left: auto;
    &-btn{
      font-weight: 600;
      margin-left: 1.25em;
    }
  }
  &__catalog-btn{
    margin-right: 2.375em;
    flex-shrink: 0;
    height: 2.5em;
    width: 9.375em;
    padding: 0 1.25em;
    justify-content: space-between;
    transition: background-color .3s;
    &-icon{
      transition: all .3s;
      background-image: url(../img/burger.svg);
      @include bgImgSize(100%);
      width: 1.3125em;
      height: 1.125em;
    }
  }
  &._show-catalog{
    .header__catalog-btn{
      pointer-events: none;
      &-icon{
        background-image: url(../img/burger2.svg);
      }
    }
    .catalog-menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
  @media screen and (min-width: 993px){
    ._desktop-hide{
      display: none;
    }
  }
}

.header-search{
  flex-shrink: 0;
  margin-left: 5.8125em;
  width: 32.375em;
  position: relative;
  &._filled{
    .header-search__result{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__field{
    position: relative;
    input{
      font-weight: 500;
      font-size: .875em;
      padding: 0 1em;
      width: 100%;
      height: 3.21em;
      border: 1px solid $grey;
      border-radius: .57em;
      font-family: inherit;
      &::placeholder{
        color: $darkGrey;
      }
    }
  }
  &__apply{
    position: absolute;
    right: .875em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__result{
    position: absolute;
    top: 100%;
    left: 0;
    padding: .625em;
    background-color: #fff;
    box-shadow: 0 .3125em .9375em rgba(0, 0, 0, 0.1);
    border-radius: .5em;
    width: 100%;
    margin-top: .5em;
    @include grid100gap(.5em);
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    z-index: 10;
  }
  &__btn{
    width: 100%;
    font-weight: 600;
    height: 2.5em;
  }
  &__close{
    display: none;
  }
}

.search-card{
  display: flex;
  align-items: center;
  &:hover{
    color: $black;
  }
  &__img{
    width: 5em;
    flex-shrink: 0;
    margin-right: 1.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    span{
      font-weight: 600;
    }
  }
  &__old-price{
    text-decoration-line: line-through;
    margin-left: .5em;
    display: inline-block;
  }
}

.menu{
  margin-top: 1.25em;
  display: flex;
  align-items: center;
  position: relative;
  &__buttons{
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  &__btn{
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    &:not(:last-child){
      margin-right: 1.25em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
    svg{
      margin-bottom: .5em;
      max-width: 1.25em;
      height: 1.125em;
      width: auto;
      fill: none;
      stroke: $black;
    }
    &:hover{
      color: $blue;
      svg{
        stroke: $blue;
      }
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link,
  &__item{
    &:not(:last-child){
      margin-right: 2.5em;
    }
  }
  &__item-show,
  &__link{
    color: $darkBlue;
    transition: all .3s;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover{
      color: $blue;
      cursor: pointer;
    }
  }
  &__item{
    position: relative;
    &:hover{
      @media screen and (min-width: 993px){
        .submenu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &-show{
      display: flex;
      align-items: center;
      img{
        width: .375em;
        margin-left: .3125em;
      }
    }
    &-hidden{
      display: none;
    }


  }
}

.submenu{
  position: absolute;
  top: 99%;
  left: -1em;
  padding-top: 1.25em;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__top,
  &__title{
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: .625em 1em;
    background: #FFFFFF;
    border: 1px solid $lightGrey;
    box-shadow: 0 0 .9375em rgba(0, 0, 0, 0.15);
    border-radius: .5em;
    min-width: 9.25em;
  }
  &__item{
    &:not(:last-child){
      margin-bottom: .625em;
    }
  }
  &__link{
    span{
      font-size: .875em;
      line-height: 140%;
    }
  }
}

.catalog-menu{
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin-top: 1.6875em;
  padding: 2.625em 3.375em 5em;
  background: #FFFFFF;
  border-radius: .8125em;
  box-shadow: 0 .25em 1.4375em rgba(0, 0, 0, 0.25);
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.125em * 3 / 4));
    gap: 2.125em;
  }
}

.catalog-group{
  display: grid;
  grid-template-columns: 3.125em calc(100% - 4.125em);
  gap: 1em;
  &__img{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    margin-top: .375em;
    @include grid100gap(.375em);
  }
}

.burger{
  display: none;
}

.header-contacts{
  margin-top: 1em;
  padding-top: 1em;
  border-top: $border;
  &__items{
    margin-top: 1em;
    @include grid100gap(.625em);
  }
  &__social{
    display: flex;
    align-items: center;
    margin-top: 1em;
    &-item{
      width: 1.6875em;
      flex-shrink: 0;
      margin-right: .5625em;
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

._show-cart._active{
  pointer-events: none;
}

.mini-cart{
  position: absolute;
  right: 0;
  top: 100%;
  margin-top: .5em;
  width: 32.125em;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  box-shadow: 0 0 .9375em rgba(0, 0, 0, 0.1);
  padding: .75em;
  background: #FFFFFF;
  border-radius: .5em;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__items{
    @include grid100gap(.5em);
    max-height: 18em;
    overflow-y: auto;
    padding-right: .5em;
    @include scroll;
  }
  .cart-card{
    grid-template-columns: 4.375em calc(100% - 16.375em) 1.25em 6.25em;
    gap: 1.5em;
    padding: 0;
    border: none !important;
    &__img{
      height: 5em;
    }
    &__delete{
      svg{
        width: 1.25em;
        height: 1.25em;
      }
    }
    &__info{
      max-width: 12.5em;
    }
    &__count{
      display: flex;
      align-items: center;
      margin-top: .875em;
      &-num{
        width: 4.6875em;
        height: 1.25em;
        @include center;
        border-radius: .5em;
        background-color: $tabGrey;
        font-weight: 600;
        margin-right: .5em;
        span{
          font-size: .875em;
          line-height: 140%;
        }
      }
    }
  }
  &__detail{
    margin-top: .5em;
    padding: .75em 0 .3125em;
    border-top: 1px solid $grey;
    background: #FFFFFF;
  }
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    font-size: 1em;
    line-height: 140%;
  }
  &__btn{
    margin-top: .625em;
    width: 100%;
    font-weight: 600;
  }
}


@media screen and (min-width: 993px){
  ._transform{
    transform: translateY(-100%);
    //height: 4.375em;
  }

  ._fixed-header{
    padding: .25em 0;
    background-color: #fff;
    box-shadow: $shadow;
    position: fixed;
    width: 100%;
    top: 0;
    transform: translateY(0);
    .header-search{
      margin-left: 14em;
      width: 28em;
    }
    .header__call-link,
    .nav{
      display: none;
    }
    .header__call{
      padding-right: 7.5em;
    }
    .menu{
      position: static;
      margin: 0;
      &__buttons{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
      &__btn{
        svg{
          margin: 0;
        }
        span{
          display: none;
        }
      }
    }
    .header__inner{
      position: relative;
    }
    .header__catalog-btn{
      position: absolute;
      top: 50%;
      left: 16.25em;
      transform: translateY(-50%);
    }
    .logo{
      &__text{
        display: none;
      }
    }
  }
}

@media screen and (min-width: 1500px){
  .header-search{
    width: 46em;
  }

  ._fixed-header .header-search{
    width: 40em;
  }
}


@media screen and (min-width: 1600px){
  .header-search{
    width: 52.25em;
  }

  ._fixed-header .header-search{
    width: 46.25em;
  }
}


@media screen and (max-width: 992px){
  .logo{
    width: 10.5em;
    height: 2em;
    position: relative;
    &__text{
      display: none;
    }
    &__img{
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }
  }

  .header{
    padding: 1.0625em 0;
    position: sticky;
    width: 100%;
    top: 0;
    background-color: #fff;
    box-shadow: $shadow;
    &__top{
      position: relative;
    }
    &__call{
      display: none;
    }
    &__mob{
      &-buttons{
        margin-left: auto;
        display: flex;
        align-items: center;
      }
      &-btn{
        width: 1.5em;
        height: 1.5em;
        margin-left: 1.25em;
        svg{
          width: 100%;
          height: 100%;
          fill: none;
          stroke: $black;
        }
        &:hover{
          svg{
            stroke: $blue;
          }
        }
      }
    }
    &__catalog-btn{
      display: none;
    }
    &._open{
      .menu{
        transform: translateX(0);
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
  }

  .header-search{
    margin: 0;
    position: absolute;
    display: grid;
    grid-template-columns: 2.5em calc(100% - 2.5em);
    background-color: #fff;
    padding: .5em .75em;
    top: -1.0625em;
    left: -1.25em;
    width: 100vw;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
    &__close{
      display: flex;
      img{
        width: 100%;
        display: block;
      }
    }
    &__result{
      margin-top: 0;
      padding: .75em;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }

  .burger{
    display: flex;
    width: 1em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    height: $fullHeight;
    width: 100%;
    max-width: 20em;
    padding: 1em 1.25em 1.75em;
    background-color: #fff;
    z-index: 20;
    margin: 0;
    display: block;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all .4s;
    transform: translateX(-200%);
    &._fixed{
      overflow: hidden;
    }
    &__bg{
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $fullHeight;
      background: $black;
      opacity: .2;
      z-index: 19;
    }
    &__top{
      display: flex;
      justify-content: space-between;
      position: relative;
    }
    &__logo{
      width: 9.625em;
      img{
        width: 100%;
      }
    }
    &__close{
      width: 3em;
      position: relative;
      z-index: 10;
      img{
        width: 100%;
        display: block;
      }
    }
    &__buttons{
      margin-top: 1em;
      padding-top: 1em;
      border-top: $border;
      flex-direction: column;
      align-items: unset;
    }
    &__btn{
      flex-direction: unset;
      &:not(:last-child){
        margin-bottom: .625em;
      }
      svg{
        width: 1.5em;
        height: 1.5em;
        margin: 0 .75em 0 0;
      }
    }
  }

  .nav{
    flex-direction: column;
    align-items: unset;
    margin-top: 2.3125em;
    &__item,
    &__link{
      &:not(:last-child){
        margin: 0 0 .5em;
      }
    }
    &__item{
      &-show{
        img{
          display: none;
        }
      }
    }
  }

  .nav,
  .submenu{
    &__item{
      &._open{
        &>.submenu{
          opacity: 1;
          pointer-events: unset;
        }
      }
      &-show{
        display: flex;
        align-items: center;
        justify-content: space-between;
        &:after{
          content: '';
          flex-shrink: 0;
          width: .375em;
          height: .625em;
          background-image: url(../img/arrow-right.svg);
          @include bgImgSize(100%);
          margin-left: 1em;
        }
        span{
          line-height: 140%;
        }
      }
    }
  }

  .submenu{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: calc(100 * var(--vh));
    background-color: #fff;
    padding: .75em 1em 3em;
    overflow-y: auto;
    &._fixed{
      overflow: hidden;
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 3em;
      margin-bottom: 1.25em;
    }
    &__title{
      display: flex;
      align-items: center;
      font-weight: 600;
      margin-bottom: 1.25em;
      span{
        font-size: 1em;
        line-height: 140%;
      }
      img{
        width: 1.125em;
        flex-shrink: 0;
        margin-left: .9375em;
      }
    }
    &__back{
      display: flex;
      align-items: center;
      font-family: $font2;
      color: $black;
      span{
        font-size: .875em;
      }
      img{
        width: 1.125em;
        flex-shrink: 0;
        margin-right: .9375em;
      }
    }
    &__links{
      padding: 0;
      box-shadow: none;
      background-color: transparent;
      border: none;
      display: block;
    }
    &__item{
      &-link{
        span{
          font-size: .875em;
          line-height: 140%;
        }
      }
      &-show{
        span{
          font-size: .875em;
        }
      }
    }
  }

  .catalog-menu,
  .mini-cart{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .logo{
    width: 6.25em;
    height: 1.25em;
  }

  .header{
    padding: .5em 0;
    ._mob-hide{
      display: none;
    }
    &__mob{
      &-btn{
        width: 1.125em;
        height: 1.125em;
        margin-left: .625em;
      }
    }
  }

  .burger{
    margin-right: .625em;
  }

  .header-search{
    top: -.5em;
    left: -.625em;
    padding-left: 0;
    padding-right: .625em;
  }
}

// end header styles
