.order{
  padding: 2.5em 0 2.75em;
}

.order-sum{
  padding: 2.5em 1em 1.25em;
  position: relative;
  .cart-sum__btn{
    height: 3.25em;
    font-weight: 600;
    font-family: $font1;
    span{
      font-size: 1em;
    }
  }
  &__confirm{
    margin-top: 1.25em;
    line-height: 130%;
    a{
      text-decoration: underline;
    }
  }
}

.buyer-form{
  padding: 1.875em 2.8125em;
  border-radius: .5em;
  border: $border2;
  &__top{
    display: flex;
    align-items: center;
  }
  &__radios{
    display: flex;
    align-items: center;
  }
  &__radio{
    margin-left: 1.5625em;
    position: relative;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      z-index: 2;
      cursor: pointer;
      &:hover + label{
        color: $blue;
      }
      &:checked + label{
        color: $blue;
        font-weight: 600;
        text-decoration: underline;
      }
    }
    label{
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__grid{
    margin-top: 1.5625em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.25em 2.5em;
  }
  &__btn{
    grid-column: 1/-1;
    margin-top: .3125em;
  }
}

.delivery-form{
  padding: 1.875em 1.25em;
  border-radius: .5em;
  border: $border2;
  margin-top: 1.875em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 2.25em;
  }
  &__city{
    font-weight: 500;
    text-decoration: underline;
    color: $darkGrey;
    font-family: $font2;
    flex-shrink: 0;
    margin-left: 1em;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover{
      color: $blue;
    }
  }
  &__search{
    margin-top: 1.25em;
    position: relative;
    img{
      position: absolute;
      width: 1.25em;
      left: 1em;
      top: 50%;
      transform: translateY(-50%);
    }
    input{
      font-weight: 500;
      font-size: 1em;
      line-height: 140%;
      font-family: $font2;
      background-color: $tabGrey;
      width: 100%;
      height: 3.625em;
      border-radius: .5em;
      border: none;
      color: #000000;
      padding: 0 1.25em 0 3.875em;
      &::placeholder{
        color: $grey2;
      }
    }
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.25em 1.875em;
    align-items: flex-start;
  }
}

.delivery-card,
.pay-way{
  input{
    position: absolute;
    z-index: 2;
    cursor: pointer;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
  }
}

.delivery-card{
  border: $border2;
  padding: 1.25em;
  position: relative;
  &__top{
    @include grid100gap(.625em);
    padding-right: 3em;
  }
  &__bottom{
    margin-top: 1.875em;
  }
  &__radio{
    input{
      &:checked+label{
        background-color: $blue;
      }
    }
    label{
      position: absolute;
      right: 1.25em;
      top: 1.25em;
      width: 1.5625em;
      height: 1.5625em;
      border-radius: 50%;
      border: .3125em solid $blue;
    }
  }
}

.address-form{
  margin-top: 5.3125em;
  &__grid{
    margin-top: 1.5625em;
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: .3125em;
    display: grid;
    grid-template-columns: 12.5em calc(100% - 12.5em - 1.875em);
    gap: 1.875em;
  }
}

.pay-ways{
  margin-top: 1.875em;
  padding: 1.875em;
  border: $border2;
  border-radius: .5em;
  &__grid{
    display: flex;
    flex-wrap: wrap;
  }
}

.pay-way{
  margin: 1.875em 1.875em 0 0;
  position: relative;
  input{
    &:checked+label:after{
      border-color: $blue;
      border-width: .3125em;
    }
    &:hover+label{
      border-color: $blue;
    }
  }
  label{
    min-height: 3.4375em;
    padding: 0 .875em;
    border-radius: .5em;
    border: $border2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    &:after{
      content: '';
      flex-shrink: 0;
      display: block;
      width: 1.5625em;
      height: 1.5625em;
      border-radius: 50%;
      border: $border2;
      margin-left: 1.875em;
      background: #F9F9FB;
    }
  }
  span{
    font-weight: 500;
    font-size: 1em;
    line-height: 140%;
  }
}


@media screen and (max-width: 992px){
  .order{
    padding: .625em 0 3.1875em;
    &__sidebar{
      position: relative;
    }
  }

  .order-sum{
    @media screen and (min-width: 577px){
      padding: 1.5em 1.25em;
      .cart-sum{
        &__form{
          grid-column: 2/3;
          gap: 1.125em;
        }
        &__btn{
          position: static;
          height: 2.75em;
          border-radius: .5em;
        }
        &__desc{
          position: absolute;
          top: 1.5em;
          left: 1.25em;
          width: calc(50% - .625em);
        }
        &__props{
          gap: 1.125em;
        }
      }
      &__confirm{
        position: absolute;
        bottom: 1.86em;
        left: 1.25em;
        width: calc(50% - .625em);
        max-width: 18.7em;
        font-size: .875em;
      }
    }
  }

  .buyer-form{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
    &__btn{
      margin-top: -.3125em;
    }
  }

  .delivery-form{
    padding: 1.25em;
    margin: 0 0 0 -1.25em;
    width: calc(100% + 2.5em);
    border: none;
    border-radius: 0;
    background-color: $lightGrey;
    &__top{
      padding-right: 2.6875em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .delivery-card{
    padding-right: 2.8125em;
    &__bottom{
      display: none;
    }
    &__radio{
      label{
        right: 2.8125em;
      }
    }
  }

  .address-form{
    padding: 1.875em 2.8125em;
    border-radius: .5em;
    border: $border2;
    margin-top: 1.875em;
    &__nav{
      grid-template-columns: 12.5em calc(100% - 12.5em - 1.5625em);
      gap: 1.5625em;
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    padding: 1.375em 0 2em;
  }

  .order-sum{
    padding-top: 1.75em;
    .cart-sum{
      &__form{
        gap: .625em;
      }
    }
    &__confirm{
      font-size: .75em;
      margin-top: 1.67em;
      max-width: 21em;
    }
  }

  .buyer-form{
    padding: .625em .625em 1.25em;
    &__top{
      display: block;
    }
    &__radios{
      margin-top: .3125em;
    }
    &__radio{
      margin: 0 .625em 0 0;
      label{
        font-size: .75em;
      }
    }
    &__grid{
      gap: .625em;
      margin-top: 1.25em;
    }
    &__btn{
      margin-top: .625em;
    }
  }

  .delivery-form{
    padding: 1.25em .625em;
    margin: 0 0 0 -.625em;
    width: calc(100% + 1.25em);
    &__top{
      padding: 0;
      display: block;
    }
    &__city{
      font-size: .875em;
      margin: .71em 0 0;
    }
    &__search{
      margin-top: .625em;
      input{
        font-size: .75em;
        height: 4.83em;
        border-radius: .67em;
        padding-left: 4.67em;
      }
    }
    &__grid{
      gap: .625em;
      margin-top: .625em;
    }
  }

  .delivery-card{
    padding-right: 1.25em;
    &__radio{
      label{
        right: 1.25em;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  .address-form{
    padding: .625em;
    margin-top: 1.25em;
    &__grid{
      gap: .625em;
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: .625em;
      grid-template-columns: 100%;
      gap: .625em;
    }
  }

  .pay-ways{
    margin-top: 1.25em;
    padding: .75em .9375em 1.5em;
    &__grid{
      @include grid100gap(.625em);
      margin-top: .625em;
    }
  }

  .pay-way{
    margin: 0;
  }
}
