//catalog page
.catalog-top{

}

.catalog-collection{
  padding: 4.375em 0 4.75em;
  background-color: $lightGrey;
  .catalog__grid{
    margin: 0;
  }
}

.catalog-list{
  padding: 1.25em 0 3.75em;
  &__grid{
    display: grid;
    grid-template-columns: 13.75em calc(100% - 15em);
    gap: 1.25em;
    margin-top: 4.25em;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__view{
    margin-top: 1.875em;
    display: none;
    &._active{
      display: block;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.6875em / 4));
    gap: 1.25em 1.5625em;
  }
  &__mob-filter{
    display: none;
  }
}

.producers{
  display: flex;
  flex-wrap: wrap;
  margin-top: -1em;
}

.producer-card{
  height: 3.875em;
  display: flex;
  align-items: center;
  border-radius: .5em;
  border: $border2;
  padding: 0 3.125em 0 1em;
  margin: 1em 1em 0 0;
  font-family: $font2;
  &__img{
    width: 3.75em;
    flex-shrink: 0;
    margin-right: .625em;
  }
}

.catalog-nav{
  @include grid100gap(.875em);
  &__item{
    &._active{
      .catalog-nav__show{
        background-color: $tabGrey;
        img{
          transform: rotate(180deg);
        }
      }
    }
  }
  &__show,
  &__link,
  &__link2{
    font-size: 1em;
    line-height: 140%;
    font-weight: 500;
  }
  &__show,
  &__link{
    padding: .25em .8125em;
    display: inline-flex;
    border-radius: .5em;
    &:hover{
      background-color: $tabGrey;
      color: $black ;
    }
  }
  &__show{
    transition: all .3s;
    cursor: pointer;
    img{
      transition: inherit;
      flex-shrink: 0;
      width: .625em;
      margin-left: .75em;
    }
  }
  &__hidden{
    display: none;
  }
  &__links2{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 2.25em;
  }
  &__link2{
    margin-top: .875em;
  }
}

.filter{
  max-width: 48.5em;
  display: flex;
  flex-wrap: wrap;
  margin-top: -.6875em;
  &__nav{
    display: none;
  }
}

.filter-select{
  margin: .6875em 1em 0 0;
  font-family: $font2;
  position: relative;
  &._active{
    .filter-select__field{
      pointer-events: none;
    }
    .filter-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .nice-select{
    font-size: 1em;
    color: $black;
    width: auto;
    font-family: $font2;
    font-weight: 400;
  }
  .nice-select,
  &__field{
    display: flex;
    align-items: center;
    padding: 0 .75em;
    height: 2.125em;
    border-radius: .5em;
    border: $border2;
    transition: all .3s;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    .count{
      padding-left: .25em;
      font-variant-numeric: tabular-nums;
      -webkit-font-feature-settings: "tnum";
      font-feature-settings: "tnum";
    }
    &:before{
      content: '';
      flex-shrink: 0;
      order: 10;
      width: .75em;
      height: .75em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: .625em;
    }
    &:hover{
      cursor: pointer;
      border-color: $blue;
    }
  }
  .nice-select .list,
  &__dropdown{
    z-index: 10;
    position: absolute;
    top: 100%;
    left: 0;
    min-width: 100%;
    padding: .625em;
    background: #FFFFFF;
    border: 1px solid $lightGrey;
    box-shadow: 0 0 .625em rgba(123, 152, 196, 0.3);
    border-radius: .5em;
    margin-top: .375em;
    @include grid100gap(.3125em);
    color: $darkGrey;
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  .m-check{
    white-space: nowrap;
  }
  .option{
    border: none;
    padding: 0;
    font-size: .875em;
    background-color: transparent !important;
    font-weight: 400 !important;
    &:not(:last-child){
      margin-bottom: .3em;
    }
    &:hover{
      color: $blue;
    }
  }
}

.views-tab{
  display: grid;
  grid-template-columns: repeat(3,1.75em);
  gap: .3125em;
  padding: .1875em .4375em;
  border-radius: .5em;
  background-color: $tabGrey;
  height: 2.125em;
  &__btn{
    width: 1.75em;
    height: 1.75em;
    @include center;
    border-radius: .375em;
    &._active,
    &:hover{
      background-color: #fff;
      svg{
        stroke: $black;
      }
      .fill-svg{
        fill: $black;
        stroke: none;
      }
    }
    svg{
      width: 1.5em;
      height: 1.5em;
      fill: none;
      stroke: #D5D5D5;
    }
    .fill-svg{
      fill: #D5D5D5;
      stroke: none;
    }
  }
}

.catalog-text{
  padding: 3.125em 0;
}

.history{
  padding: 2.5em 0 3.75em;
  overflow: hidden;
  .swiper{
    &-slide{
      width: 16.25em;
    }
  }
}

.history-card{
  display: block;
  padding: 1.25em 1.25em 1em;
  border-radius: .5em;
  border: $border2;
  height: 100%;
  &:hover{
    color: $black;
    border-color: $blue;
  }
  &__img{
    @include imgCover(51.57%);
  }
  &__content{
    margin-top: .625em;
    @include grid100gap(.1875em);
  }
}

.content-nav{
  margin-top: 2.3125em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__more{
    height: 2.5em;
    width: 29em;
    max-width: 100%;
    font-weight: 500;
    font-family: $font2;
  }
}

.pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.4375em;
  &__btn{
    min-width: 2.0625em;
    height: 2.5em;
    border-radius: .5em;
    @include center;
    color: $blue;
    font-weight: 500;
    font-family: $font2;
    margin: 0 .15625em;
    padding: 0 .625em;
    font-variant-numeric: tabular-nums;
    -webkit-font-feature-settings: "tnum";
    font-feature-settings: "tnum";
    span{
      font-size: 1.25em;
    }
    &:hover,
    &._active{
      background-color: $blue;
      color: #fff;
    }
    &._dots{
      pointer-events: none;
    }
  }
  &__next,
  &__prev{
    display: flex;
    align-items: center;
    svg{
      width: .5em;
      height: .875em;
      fill: none;
      stroke: $blue;
    }
  }
  &__prev{
    margin-right: .5em;
  }
  &__next{
    margin-left: .5em;
  }
}


@media screen and (max-width: 992px){
  .catalog-collection{
    padding: 1.25em 0;
  }

  .catalog-list{
    padding-bottom: 1.875em;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__sidebar{
      display: none;
    }
    &__view{
      margin-top: 1.25em;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 3.125em / 3));
    }
  }

  .producers{
    margin: 0;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
  }

  .producer-card{
    margin: 0;
    padding: 0 .625em 0 .875em;
  }

  .filter{
    max-width: calc(100% - 6.75em);
  }

  .filter-select{
    margin-right: .875em;
  }

  .catalog-text{
    padding: 1.875em 0;
  }

  .history{
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-list{
    padding: .25em 0 1em;
    &__grid{
      margin-top: 2em;
    }
    &__mob-filter{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .filter-select{
        margin: 0;
      }
    }
    &__toggle-filter{
      display: flex;
      align-items: center;
      font-weight: 600;
      span{
        font-size: .75em;
      }
      img{
        width: .9375em;
        flex-shrink: 0;
        margin-right: .5625em;
      }
    }
    &__view{
      margin-top: 1em;
      &._mob-view{
        display: block;
      }
      &:not(._mob-view){
        display: none;
      }
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .1875em));
      gap: .375em;
    }
  }

  .producers{
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }

  .producer-card{
    height: 3.125em;
    padding: 0 .5em 0 .625em;
    &__img{
      width: 2.9375em;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
    }
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    margin: 0;
    background-color: #fff;
    z-index: 100;
    max-width: unset;
    flex-direction: column;
    padding: .625em .625em 3.25em;
    overflow-y: auto;
    flex-wrap: nowrap;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    ._mob-hide{
      display: none;
    }
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
    &__nav{
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: .625em;
      background-color: #fff;
      align-items: center;
      z-index: 5;
      display: grid;
      grid-template-columns: calc(100% - 1.875em) 1.25em;
      gap: .625em;
      border-top: $border2;
    }
    &__close{
      img{
        width: 100%;
      }
    }
    &__btn{
      height: 2em;
      span{
        font-size: .75em;
      }
    }
  }

  .filter-select{
    margin: 0 0 1.25em;
    &:not(._order){
      order: 10;
    }
    &._mob-show{
      .filter-select__dropdown{
        display: block;
      }
      .filter-select__field{
        &:before{
          transform: rotate(180deg);
        }
      }
      &._mob-select{
        .filter-select__field{
          &:before{
            transform: unset;
          }
        }
      }
    }

    &._active{
      .filter-select__field{
        pointer-events: unset;
        &:before{
          transform: rotate(180deg);
        }
      }
      .filter-select__dropdown{
        //display: block;
      }
    }
    &__field{
      height: unset;
      font-weight: 500;
      span{
        font-size: .75em;
      }
    }
    .nice-select{
      padding: 0 .75em;
      height: 1.8125em;
      span{
        font-size: .75em;
      }
      .option{
        font-size: .75em;
      }
    }
    &__dropdown{
      opacity: 1;
      pointer-events: unset;
      display: none;
      position: static;
      transition: unset;
      box-shadow: none;
      padding: 0 0 0 .3125em;
      background-color: transparent;
      border: none;
    }
    &__field{
      border: none;
      padding: 0;
      &:before{
        margin-left: auto;
      }
    }
    .m-check{
      &:not(:last-child){
        margin-bottom: .5em;
      }
    }
    &._switches{
      .filter-select__dropdown{
        display: block !important;
        margin: 0;
        padding: 0;
      }
      .filter-select__field{
        display: none;
      }
      .m-check{
        font-weight: 500;
        color: $black;
        input{
          &:checked+label{
            &:before{
              background-color: $lightBlue;
              border-color: $lightBlue;
            }
            &:after{
              background-color: #fff;
              right: .1875em;
            }
          }
        }
        label{
          padding-right: 3em;
          &:before,
          &:after{
            top: 50%;
            transform: translateY(-50%);
            transition: all .3s;
          }
          &:before{
            position: absolute;
            right: 0;
            margin: 0;
            border-radius: 625em;
            width: 2em;
            height: 1em;
          }
          &:after{
            content: '';
            position: absolute;
            right: 1.1875em;
            background-color: $blue;
            border-radius: 50%;
            width: .625em;
            height: .625em;
            z-index: 2;
          }
        }
      }
    }
  }

  .views-tab{
    display: none;
  }

  .history{
    .swiper{
      &-slide{
        width: 15.8125em;
      }
    }
  }

  .content-nav{
    &__more{
      height: 2.1875em;
      span{
        font-size: .75em;
      }
    }
  }

  .pagination{
    margin-top: 1.25em;
    &__btn{
      min-width: 1.875em;
      height: 2.125em;
      margin: 0 .125em;
      padding: 0 .5em;
      span{
        font-size: 1em;
      }
    }
  }
}
//end catalog page
