.brands-page{
  padding: 1.25em 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 9.375em / 6));
    gap: 1.875em;
  }
  .brand-card{
    display: block;
    border-radius: .5em;
    border: $border2;
    padding: .625em;
    &:hover{
      border-color: $blue;
    }
    &__img{
      padding-bottom: 64.5%;
    }
  }
}

.brand-item{
  padding: 1.25em 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: 23.75em calc(100% - 23.75em - 1.875em);
    gap: 1.875em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .brands-page{
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
      gap: .625em;
    }
  }

  .brand-item{
    padding: 0 0 1.875em;
    &__grid{
      grid-template-columns: 17.5em calc(100% - 17.5em - 2.25em);
      gap: .9375em 2.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .brands-page{
    padding-top: 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .5em .625em;
    }
    .brand-card{
      padding: .5em;
    }
  }

  .brand-item{
    padding-bottom: 1.25em;
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
