.contacts{
  padding-bottom: 7em;
  &__grid{
    display: grid;
    grid-template-columns: 30em calc(100% - 31.875em);
    gap: 1.875em;
  }
  &__items{
    @include grid100gap(2em);
  }
}

.contact-group{
  font-family: $font2;
  @include grid100gap(.5em);
  &__title{
    font-size: 1em;
    line-height: 140%;
    font-weight: 500;
    color: #454545;
  }
  &__desc{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 1.125em;
    line-height: 140%;
    color: #000000;
    p{
      line-height: inherit;
    }
    a{
      line-height: inherit;
    }
  }
}

.map{
  min-height: 100%;
  height: 30em;
  iframe{
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.875em;
    }
    &__map{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__map{
      width: calc(100% + 1.25em);
      margin-left: -.625em;
    }
  }
}
