.fg {
  position: relative;
  margin: 0;
  font-family: $font2;
  label{
    font-size: 1em;
    line-height: 140%;
    color: $grey2;
    font-family: inherit;
    display: block;
    margin-bottom: .625em;
  }
  textarea,
  input {
    font-family: inherit;
    border: $border2;
    padding: 0 1.64em;
    height: 4.28em;
    width: 100%;
    font-size: .875em;
    background-color: transparent;
    color: $black;
    border-radius: .57em;
    &::placeholder {
      color: $grey2;
    }
    &:focus{
      border-color: $blue;
    }
  }
  textarea{
    resize: none;
    height: 9.375em;
  }
  &__hint{
    font-size: .875em;
    line-height: 140%;
    color: $darkGrey;
    margin-top: .3em;
    font-family: inherit;
    padding-right: 1.5em;
  }
  &._small{
    input{
      height: 2.71em;
    }
  }
  &._required{
    label{
      &:after{
        content: '*';
        color: $black;
      }
    }
  }
}

.switch,
.m-check,
.m-radio {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:hover+label{
      color: $blue;
      &:before{
        border-color: $blue;
      }
    }
  }
}

.m-check,
.m-radio{
  label {
    display: flex;
    min-height: 1.25em;
    font-size: 1em;
    cursor: pointer;
    font-weight: 500;
    &:before {
      content: '';
      z-index: 1;
      border: $border;
      width: .6875em;
      height: .6875em;
      transition: background-color .100s, border-color .100s;
      flex-shrink: 0;
      margin-right: .625em;
      background-repeat: no-repeat;
      background-position: center;
      transform: translateY(.28125em);
    }
    span{
      font-size: .875em;
      line-height: 140%;
    }
    a{
      position: relative;
      z-index: 2;
      color: $blue;
    }
  }
  &._not-label{
    label{
      &:before{
        margin-right: 0;
      }
    }
  }
}

.m-check{
  input{
    &:checked+label {
      &::before {
        border-color: $blue;
        background-color: $blue;
      }
    }
  }
  label{
    &:before{
      border-radius: .1875em;
      background-size: .5em auto;
    }
  }
}

.cartcalc{
  width: 12em;
  flex-shrink: 0;
  max-width: 100%;
  display: grid;
  grid-template-columns: 1.5em calc(100% - 3em) 1.5em;
  align-items: center;
  background-color: $tabGrey;
  height: 3.125em;
  border-radius: .5em;
  padding: 0 .6875em;
  input{
    font-size: 1.125em;
    padding: 0;
    text-align: center;
    font-weight: 600;
    background: transparent;
    border: none;
    font-family: $font2;
  }
  button{
    width: 100%;
    svg{
      width: 100%;
      height: 1.5em;
      fill: none;
      stroke: #2A353D;
    }
    &:hover{
      svg{
        stroke: $blue;
      }
    }
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1em;
    display: flex;
    align-items: center;
    svg{
      width: 100%;
      height: auto;
    }
    path{
      fill: #E0E0E0;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: #FFB800;
      }
      &~.rating-input__item {
        path{
          fill: #FFB800;
        }
      }
    }
  }
  &__text{
    order: -1;
    font-size: 1em;
    line-height: 140%;
    hanging-punctuation: first last;
    text-decoration-line: underline;
    font-feature-settings: 'case' on, 'numr' on, 'calt' off;
    color: $black;
    display: flex;
    align-items: center;
    &:before{
      content: '';
      display: block;
      width: .625em;
      height: .0625em;
      background-color: $black;
      flex-shrink: 0;
      margin: 0 .625em 0 1.5em;
    }
  }
  ._view1,
  ._view2,
  ._view3,
  ._view4,
  ._view5{
    display: none;
  }
  &:not([data-total-rating="0"]){
    ._view0{
      display: none;
    }
  }
  &[data-total-rating="1"]{
    ._view1{
      display: block;
    }
  }
  &[data-total-rating="2"]{
    ._view2{
      display: block;
    }
  }
  &[data-total-rating="3"]{
    ._view3{
      display: block;
    }
  }
  &[data-total-rating="4"]{
    ._view4{
      display: block;
    }
  }
  &[data-total-rating="5"]{
    ._view5{
      display: block;
    }
  }
  &__item{
    &[data-rating-value="1"]:hover{
      &~.rating-input__text{
        ._view0{
          display: none;
        }
        ._view1{
          display: block;
        }
      }
    }
    &[data-rating-value="2"]:hover{
      &~.rating-input__text{
        ._view0{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &[data-rating-value="3"]:hover{
      &~.rating-input__text{
        ._view0{
          display: none;
        }
        ._view3{
          display: block;
        }
      }
    }
    &[data-rating-value="4"]:hover{
      &~.rating-input__text{
        ._view0{
          display: none;
        }
        ._view4{
          display: block;
        }
      }
    }
    &[data-rating-value="5"]:hover{
      &~.rating-input__text{
        ._view0{
          display: none;
        }
        ._view5{
          display: block;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: #FFB800;
  }
}

.file-input{
  display: flex;
  align-items: center;
  position: relative;
  padding: .625em;
  border-radius: .5em;
  border: $border2;
  input{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    &:hover+*{
      color: $blue;
    }
  }
  img{
    width: 1.5em;
    flex-shrink: 0;
    display: block;
    margin-right: .625em;
  }
}


@media screen and (max-width: 576px){
  .fg{
    input{
      height: 3.71em;
    }
    label{
      font-size: .75em;
      margin-bottom: .5em;
    }
    &__hint{
      font-size: .75em;
      margin-top: .83em;
    }
  }

  .m-check{
    label{
      span{
        font-size: .75em;
      }
      &:before{
        transform: translateY(.15em);
      }
    }
  }

  .cartcalc{
    width: 6.75em;
    height: 2.75em;
    grid-template-columns: 1.375em calc(100% - 2.75em) 1.375em;
    padding: 0 .5625em;
    input{
      font-size: 1.125em;
    }
    button{
      svg{
        height: 1.375em;
      }
    }
  }
}
