.qa{
  overflow: hidden;
  .product-detail__top{
    margin-top: -.9375em;
  }
  .tab-content{
    margin-top: 3em;
  }
  &__items{
    padding: 0 1.25em;
    border: $border2;
    border-radius: .5em;
  }
}

.qa-card{
  padding: 1.25em 0;
  font-family: $font2;
  &:not(:last-child){
    border-bottom: $border2;
  }
  &._active{
    .qa-card__btn{
      transform: rotate(45deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    font-size: 1.125em;
    line-height: 140%;
    font-weight: 500;
  }
  &__btn{
    width: 1em;
    flex-shrink: 0;
    margin-left: 2em;
    transition: all .4s;
    img{
      width: 100%;
      display: block;
    }
  }
  &__hidden{
    margin-top: .9375em;
    display: none;
  }
}

.consult{
  border-radius: .5em;
  margin-top: 3em;
  max-width: 52.1875em;
  border: $border2;
  padding: 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__content{
    @include grid100gap(1.25em);
    max-width: 30em;
    font-family: $font2;
  }
  &__nav{
    flex-shrink: 0;
    margin-left: 1em;
  }
  &__btn{
    white-space: unset;
    width: 13.75em;
    height: 2.625em;
    font-weight: 600;
    padding: 0 1em;
  }
}


@media screen and (max-width: 992px){
  .qa{
    padding-bottom: 3.5625em;
    .tab-content{
      margin-top: 2.125em;
    }
    &__items{
      padding: 0 1.25em;
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      border-left: none;
      border-right: none;
      border-radius: 0;
    }
  }

  .consult{
    margin-top: 1.6875em;
    border-radius: .5em;
  }
}


@media screen and (max-width: 576px){
  .qa{
    padding-bottom: 1.875em;
    .tab-content{
      margin-top: 2.125em;
    }
    &__items{
      padding: 0;
      width: 100%;
      margin: .625em 0 0;
    }
  }

  .qa-card{
    .text{
      max-width: 13.75em;
    }
  }

  .consult{
    margin-top: 1.5em;
    padding: 0;
    border: none;
    display: block;
    .text32{
      font-weight: 600;
    }
    &__nav{
      margin: 1.5em 0 0;
    }
  }
}
