//cards styles
.catalog-card,
.catalog-card2{
  &__nav{
    display: grid;
    grid-template-columns: calc(100% - 1.875em) 1.0625em;
    gap: .8125em;
  }
  &__btn{
    padding: 0;
  }
  &__badges{
    position: absolute;
    top: 1.25em;
    left: 0;
    width: 100%;
    padding: 0 .9375em 0 1.25em;
  }
}

.catalog-card{
  height: 100%;
  padding: 1.25em;
  background: #FFFFFF;
  border: 1px solid $tabGrey;
  border-radius: .5em;
  position: relative;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  &:hover{
    box-shadow: $shadow;
    .catalog-card__detail{
      opacity: 1;
    }
  }
  &__detail{
    opacity: 0;
    box-shadow: 0 0 .625em rgba(123, 152, 196, 0.3);
    font-family: $font2;
    position: absolute;
    top: 6.6875em;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    padding: 0 .4375em;
    height: 1.5625em;
    img{
      width: .9375em;
      flex-shrink: 0;
      margin-right: .3125em;
    }
    span{
      font-size: .625em;
    }
  }
  &__img{
    padding-bottom: 111.67%;
    position: relative;
    display: block;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: .625em;
    @include grid100gap(.3125em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: .625em;
    @include grid100gap(.625em);
  }
  &__info2{
    display: flex;
    align-items: center;
  }
  &__rating{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      display: block;
      flex-shrink: 0;
      margin-right: .125em;
    }
    span{
      font-size: .75em;
    }
  }
  &__has{
    margin-left: .875em;
  }
  &__nav{
    min-height: 2.5em;
    &._active{
      .cartcalc{
        display: grid;
      }
      .catalog-card__btn{
        display: none;
      }
    }
  }
  &__btn{
    height: 2.5em;
    width: 100%;
  }
  .cartcalc{
    display: none;
    padding: 0;
    width: 100%;
    border: 1px solid $blue;
    background-color: transparent;
    height: 2.375em;
    grid-template-columns: 2.375em calc(100% - 4.75em) 2.375em;
    button{
      background-color: $blue;
      height: 100%;
      svg{
        height: 1.125em;
        stroke: #fff;
      }
    }
    .ccalc-minus{
      border-radius: .5em 0 0 .5em;
      transform: translateX(-1px);
    }
    .ccalc-plus{
      border-radius: 0 .5em .5em 0;
      transform: translateX(1px);
    }
  }
}

.catalog-card2{
  display: grid;
  grid-template-columns: 11.875em calc(100% - 27.53em) 11.875em;
  gap: 1.875em;
  padding: 1.875em 0;
  border-bottom: $border2;
  position: relative;
  &:first-child{
    padding-top: 0;
  }
  &:last-child{
    padding-bottom: 0;
    border-bottom: none;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    padding-right: 2.25em;
  }
  &__prices{
    text-align: right;
    &-new{
      font-size: 1.125em;
      line-height: 140%;
      font-weight: 600;
    }
    &-old{
      color: $grey2;
      font-size: .875em;
      line-height: 140%;
      text-decoration: line-through;
    }
  }
  &__nav{
    width: calc(100% + 2.25em);
    margin-right: -2.25em;
    margin-top: .6875em;
  }
  &__badges{
    top: 0;
    padding: 0;
    max-width: 12.5em;
  }
  .props{
    margin-top: .75em;
    &__items{
      margin-top: 1.125em;
      font-family: $font2;
    }
  }
  .product-desc{
    margin-top: .75em;
    &__nav{
      margin-top: .75em;
    }
  }
}

.catalog-card3{
  .product-review__rating{
    margin: .375em 0 0;
  }
  &__text{
    margin-top: .375em;
    p{
      line-height: inherit;
    }
  }
}

.service-card,
.promo-card{
  &__img{
    img{
      border-radius: .5em;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.9375em);
  }
  &__title{
    @include truncate(2);
  }
}

.service-card{
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(51.3%);
    img{
      border-radius: .5em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: .625em;
    display: flex;
  }
  &__btn{
    padding: 0 1.25em;
    height: 2.375em;
    span{
      font-size: .875em;
    }
  }
  &__desc{
    @include truncate(4);
  }
}

.promo-card{
  &__img{
    @include imgCover(63.15%);
  }
  &__desc{
    color: #838383;
  }
}

.advantage-card{
  display: grid;
  grid-template-columns: 3.75em auto;
  gap: 1.5em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: .9375em;
  }
}

.review-card{
  display: block;
  &__top{
    position: relative;
  }
  &__img{
    margin-bottom: 1.25em;
    @include imgCover(61.6%);
    img{
      border-radius: .5em;
    }
  }
  &__title{
    @include truncate(2);
  }
}

.brand-card{
  display: block;
  &__img{
    position: relative;
    padding-bottom: 50%;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.tariff-card{
  display: flex;
  flex-direction: column;
  padding: 1.25em;
  background: #FFFFFF;
  border-radius: .5em;
  border: 1px solid $tabGrey;
  &__top{
    margin-top: .625em;
  }
  &__info{
    margin-top: 1em;
    font-size: .625em;
    line-height: 140%;
    color: $grey;
    max-width: 17em;
    font-weight: 500;
    p{
      line-height: inherit;
    }
    span{
      font-weight: 600;
      font-size: 1.4em;
      line-height: 140%;
    }
  }
  &__bottom{
    margin-top: .5em;
  }
  &__nav{
    margin-top: auto;
    padding-top: .8125em;
  }
  &__btn{
    padding: 0;
    width: 100%;
    height: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .catalog-card{
    &__detail{
      display: none;
    }
  }

  .review-card{
    &__img{
      margin-bottom: 1.5em;
    }
  }

  .catalog-card2{
    grid-template-columns: 11.875em calc(100% - 26.25em) 11.875em;
    gap: 1.25em;
  }
}



@media screen and (max-width: 576px){
  .catalog-card{
    padding: .625em;
    &__badges{
      top: .625em;
      padding: 0 .375em 0 .625em;
    }
    &__info{
      margin-top: .3125em;
      gap: 0;
    }
    &__bottom{
      padding-top: .3125em;
      gap: .5em;
    }
    &__rating{
      span{
        font-size: .75em;
      }
    }
    &__nav{
      grid-template-columns: auto .75em;
      gap: .5em;
    }
    &__btn{
      height: 1.75em;
      padding: 0;
      border-radius: .3125em;
      span{
        font-size: .625em;
      }
    }
    .favourite-btn{
      &__icon{
        width: .75em;
      }
    }
  }

  .service-card{
    &__img{
      padding-bottom: 64.7%;
    }
  }

  .promo-card{
    &__img{
      padding-bottom: 79.73%;
    }
  }

  .tariff-card{
    padding: .5em;
    .text20{
      font-size: 1em;
    }
    &__info{
      margin-top: .625em;
      font-size: .5em;
    }
    &__bottom{
      margin-top: .3125em;
      .text12{
        font-size: .5em;
      }
    }
    &__nav{
      padding-top: .375em;
    }
    &__btn{
      height: 1.6875em;
      span{
        font-size: .625em;
      }
    }
  }
}
//end cards styles
